.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    background-color:aliceblue;
    .container{
        width: 100%;
        .homeImage{
            height: 400px;
            width: 100%;
            object-fit: cover;
            z-index: -1;
            margin-bottom: -50px;
            mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,1));
        }
        .homeRow{
            display: flex;
            z-index: 1;
            margin-left: 5px;
            margin-right: 5px;
        }
    }
}